:root{
    --first-color: #4070F4;
    --second-color: #0E2431;
}

body{
    //margin: var(--header-height) 0 0 0;
    font-family: var(--body-font);
    font-size: var(--normal-font-size);
    color: var(--second-color);
}

img{
    max-width: 100%;
    height: auto;
    display: block; // borra los espacios en blanco
}

/*===== HOME =====*/
.home{
    height: calc(100vh - 3rem);
    row-gap: 1rem;

    &__data{
        align-self: center;
    }
    &__title{
        font-size: var(--big-font-size);
        margin-bottom: var(--mb-5);

        &-color{
            color: var(--first-color);
        }
    }
    &__social{
        //display: flex;
        //flex-direction: row;
        width: 100%;
        position: relative;
        top: -10%;
        text-align: center;
        vertical-align: bottom;
        &-icon{
          //position: relative;
          display: inline-block;
          //left: 45%;

            width: max-content;
            margin-bottom: var(--mb-2);
            font-size: 3rem;
            color: var(--second-color);

            &:hover{
                color: var(--first-color);
            }
        }
    }

    /*&__img{
        position: absolute;
        right: 0;
        bottom: 0;
        width: 295px;
    }*/
}

/*BUTTONS*/
.button{
    display: inline-block;
    background-color: var(--first-color);
    color: #fff;
    padding: .75rem 2.5rem;
    font-weight: var(--font-semi);
    border-radius: .5rem;

    &:hover{
        box-shadow: 0px 10px 36px rgba(0,0,0,.15);
    }
}

/* ===== ABOUT =====*/
.about{
    &__container{
        row-gap: 2rem;
        text-align: center;
    }
    &__subtitle{
        margin-bottom: var(--mb-2);
    }

    &__img{
        justify-self: center;

        & img{
            width: 200px;
            border-radius: .5rem;
        }
    }
}

/* ===== SKILLS =====*/
.skills{
    &__container{
        row-gap: 2rem;
        text-align: center;
    }
    &__subtitle{
        margin-bottom: var(--mb-2);
    }
    &__text{
        margin-bottom: var(--mb-4);
    }
    &__data{
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        font-weight: var(--font-semi);
        padding: .5rem 1rem;
        margin-bottom: var(--mb-4);
        border-radius: .5rem;
        box-shadow: 0px 4px 25px rgba(14, 36, 49, 0.15);
    }
    &__icon{
        font-size: 2rem;
        margin-right: var(--mb-2);
        color: var(--first-color);
    }
    &__names{
        display: flex;
        align-items: center;
    }
    &__bar{
        position: absolute;
        left: 0;
        bottom: 0;
        background-color: var(--first-color);
        height: .25rem;
        border-radius: .5rem;
        z-index: var(--z-back);
    }
    //--- each skill ---
    &__java{
        width: 90%;
    }
    &__c{
        width: 75%;
    }
    &__js{
        width: 80%;
    }
    &__py{
        width: 60%;
    }
    &__img{
        border-radius: .5rem;
    }
    &__html{
      width: 70%;
    }
}
/* ===== WORK =====*/
.projects{
    &__container{
        row-gap: 2rem;
        height: calc(50vh - 3rem);
    }
    /*&__img{
        box-shadow: 0px 4px 25px rgba(14, 36, 49, 0.15);
        border-radius: .5rem;
        overflow: hidden;

        & img{
            transition: 1s;
            cursor: pointer;

            &:hover{
                transform: scale(1.1);
            }
        }
    }*/

}

.proj{
  width: 100%;
  height: 200px;
}

.proj:hover .burger{
  opacity: 1;
}

#nhsfinder{
  & .burger{
    color: var(--first-color);
    float: right;
    position: relative;
    left: -20rem;
    opacity: 0;
  }
}


/* ===== CONTACT =====*/
.contact{
    &__input{
        width: 100%;
        font-size: var(--normal-font-size);
        font-weight: var(--font-semi);
        padding: 1rem;
        border-radius: .5rem;
        border: 1.5px solid var(--second-color);
        outline: none;
        margin-bottom: var(--mb-4);
    }
    &__button{
        display: block;
        border: none;
        outline: none;
        font-size: var(--normal-font-size);
        cursor: pointer;
        margin-left: auto;
    }
}
